import {combineReducers, createStore} from "redux";
import {hwReducer} from "./homeWorkReducer/HomeWorkReducer";
import {juniorReducer} from "./juniorReducer/juniorReducer";

let reducers = combineReducers({
    hw:hwReducer,
    juniorReducer: juniorReducer

});
export type RootState = ReturnType<typeof reducers>
export const store = createStore(reducers)

// @ts-ignore
window.store = store